@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100%;
  margin: 0;
}


html, body, #app, #app>div {
    height: 100%;
    margin: 0;
  }
body {
    
    background-color: #d6d6d6;
}
.background {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: middle;
            align-items: middle;
}
.connect-four {
    justify-self: middle;
    font-family: 'Lato', sans-serif;
    width: 50%;
    min-width: 500px;
    margin: auto;
    margin-top: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #6C838C;
    box-shadow: 0 0px 10px 2px black;
    }

.header {
    background-color: #243340;
    position: relative;
    z-index: 2;
}

.restart-button {
	background-color:#D97762;
	border-radius:28px;
	border:1px solid #D97762;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	padding:5px 31px;
	text-decoration:none;
	text-shadow:0px 1px 0px grey;
}
.restart-button:hover {
	background-color:#c76d5a;
    cursor: pointer;
}

h1 {
    margin: 0;
    margin-top: 30px;
    color: #D97762;
    font-size: 60px;
    text-align: center;
    text-shadow: 0px 1px 0px grey;
}
h2 {
    margin: 0;
    color: grey;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 20px;
    text-align: center;
}
.menu {
    position: relative;
    z-index: 2;
    width: 100%;
    background-color: #243340;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 10px;
    gap: 10px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.board {
    margin: 100px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    
    box-shadow: 0 0 5px black;
}

.col {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #454545;
}

.col:hover {
    background-color: #545454;
    cursor: pointer;
}

.cell {
    width: 40px;
    height: 40px;
    margin: 10px;
    border-radius: 100%;
}


.empty {
    background-color: #fff;
}

@-webkit-keyframes animateIn {
    from {top:-400px;}
    to {top: 0px;}
}

@keyframes animateIn {
    from {top:-400px;}
    to {top: 0px;}
}

.p1 {
    position: relative;
    -webkit-animation-name: animateIn;
            animation-name: animateIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    background-color: #D13237;
    z-index: 1;
}

.p2 {
    background-color: #F2C85C;
    -webkit-animation-name: animateIn;
            animation-name: animateIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    position: relative;
    z-index: 1;

}
